/* About Me */
.about-me {
  &__title h1 {
    text-align: center;
  }

  &__container {
    width: 90%;
    margin: 0 5%;
  }

  &__photo--container {
    margin: 0 auto;
  }

  &__description {
    p {
      font-size: 1.3rem;
      width: 90%;
      margin: 0 auto;
      line-height: 2rem;
    }
  }

  &__image {
    vertical-align: middle;
    border-style: none;
    filter: grayscale(30%);
    width: 80%;
    border-radius: 1rem;
    margin: 0 10%;
  }
}

@media (max-width: 995px) {
  .about-me {
    &__description {
      & p {
        width: 100%;
        font-size: 1.2rem;
        line-height: 1.5rem;
      }
    }
  }
}

@media (max-width: $tablet) {
  .about-me {
    &__image {
      width: 50%;
      margin: 0 25% 5rem 25%;
    }
    &__description {
      & p {
        width: 80%;
        font-size: 1rem;
      }
    }
  }
}
