.emphasize {
  font-weight: 600;
  color: $neonDark;
}

.emphasize-title {
  border-bottom: 0.2rem solid $neonDark;
}

.center {
  margin: 0 auto;
}

.hidden {
  display: none;
}

.section__title {
  text-align: center;
  margin: 1.5rem;
  h1 {
    font-size: 3rem;
    padding: 1.5rem 0;
  }
}

.section__title.first {
  margin-top: 1rem;
}

@media (max-width: $mobileLarge) {
  .section__title {
    h1 {
      font-size: 1.5rem;
    }
  }
}
